export default "#iframe--industrial-layout-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * slider\n      */\n    .slider {\n      .slides-inner {\n        overflow: hidden;\n        // dragger animations\n        .transition-none() !important;\n        .transform-none() !important;\n      }\n      .slide {\n        .toggle();\n        .col(12);\n      }\n      .featured-item {\n        position: relative;\n      }\n      .slide-pagination-content {\n        display: none;\n      }\n      // navigation\n      [data-xt-nav].btn {\n        .btn-default();\n        font-size: 10px;\n        [class^='icon-'], [class*=' icon-'] {\n          .icon-md();\n        }\n      }\n      &.slider--nonav {\n        [data-xt-nav].btn {\n          display: none !important;\n        }\n      }\n      // .featured-item\n      .slide_card {\n        @media @max-sm {\n          .container-space-remove();\n        }\n        @media @min-sm {\n          position: absolute;\n          bottom: 2.5rem;\n          right: 2.5rem;\n          width: 49rem;\n        }\n        > .card {\n          .card-squared();\n          align-items: flex-end;\n        }\n      }\n      .slide_card_inner {\n        overflow: hidden;\n      }\n      .featured-item_asset {\n        position: relative;\n        background: @shade-100;\n        .media-container {\n          overflow: hidden;\n          background: transparent;\n        }\n        .media-inner {\n          overflow: hidden;\n        }\n      }\n      .featured-item_assets {\n        @media @max-sm {\n          .container-space-remove();\n        }\n        > .row {\n          .row-default();\n          .row-y(none);\n        }\n      }\n      .featured-item_assets_col--big {\n        .col(12);\n        .col(8, sm);\n      }\n      .featured-item_assets_col--sm {\n        .col(12);\n        .col(4, sm);\n        @media @max-sm {\n          display: none;\n        }\n      }\n      .featured-item_asset {\n        .media-container {\n          height: 25rem;\n          @media @min-sm {\n            height: 49rem;\n          }\n        }\n      }\n      // pagination\n      .slider-pagination {\n        .container-space();\n        .list(mini);\n        flex-wrap: nowrap;\n        align-items: center;\n        justify-content: center;\n        .padding(bottom, big);\n      }\n      [data-xt-pag].btn {\n        .btn-default();\n      }\n      // pagination wrap\n      .slider-pagination_inner {\n        position: relative;\n        overflow: hidden;\n        white-space: nowrap;\n        [data-xt-pag] {\n          .margin(x, mini);\n        }\n        &:before, &:after {\n          content: '';\n          pointer-events: none;\n          position: absolute;\n          z-index: @index-active + 1;\n          top: 0;\n          bottom: 0;\n          width: 30px;\n        }\n        &:before {\n          left: 0;\n          background: linear-gradient(-90deg, fade(@white, 0%) 0%, @white 100%);\n        }\n        &:after {\n          right: 0;\n          background: linear-gradient(90deg, fade(@white, 0%) 0%, @white 100%);\n        }\n      }\n      .slider-cards--logos {\n        .slider-pagination_inner {\n          max-width: none;\n        }\n      }\n      .slider-pagination_scroll {\n        display: inline-block;\n        position: relative;\n        .padding(x, 30px);\n        transition: left 750ms @ease-out;\n      }\n    }\n\n  }\n  /*##END*/\n}\n";