export default "module.exports = {\n  boxShadow: {\n    overlay: '0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)',\n  },\n  utility: {\n    '.overlay-disable': {\n      // setup\n      '@apply xt-disable': '',\n      display: 'flex !important',\n      overflow: 'initial !important',\n      position: 'static',\n      zIndex: 'initial',\n      top: 'auto',\n      left: 'auto',\n      bottom: 'auto',\n      right: 'auto',\n      width: 'auto',\n      height: 'auto',\n      '.overlay-container': {\n        zIndex: 'initial',\n        padding: '0',\n        width: '100%',\n        minHeight: 'auto',\n        maxWidth: 'none',\n      },\n      '.overlay-inner': {\n        '> .card': {\n          '@apply card-disable !important': '',\n        },\n      },\n      '> .backdrop': {\n        display: 'none !important',\n      },\n    },\n  },\n  component: theme => ({\n    '.overlay': {\n      // setup\n      '@apply toggle overflow-main': '',\n      position: 'fixed',\n      zIndex: theme('zIndex.overlay'),\n      top: '0',\n      left: '0',\n      bottom: '0', // @FIX no height or it bugs collapse animation\n      right: '0', // @FIX no width or it bugs collapse animation\n      overflow: 'hidden',\n      '&.out': {\n        pointerEvents: 'none',\n      },\n    },\n    '.overlay-container': {\n      // setup\n      display: 'flex',\n      flexWrap: 'nowrap',\n      flexDirection: 'column',\n      alignItems: 'center',\n      justifyContent: 'center',\n      margin: 'auto', // @FIX http://stackoverflow.com/questions/33454533/cant-scroll-to-top-of-flex-item-that-is-overflowing-container\n      width: '100%',\n      minHeight: '100vh',\n      padding: theme('container.padding.DEFAULT'),\n      '@screen sm': {\n        padding: theme('container.padding.sm'),\n      },\n      '@screen md': {\n        padding: theme('container.padding.md'),\n      },\n      '@screen lg': {\n        padding: theme('container.padding.lg'),\n      },\n      '@screen xl': {\n        padding: theme('container.padding.xl'),\n      },\n    },\n    '.overlay-inner': {\n      // setup\n      display: 'flex',\n      flexDirection: 'column',\n      justifyContent: 'center',\n      alignItems: 'center',\n      position: 'relative',\n      zIndex: theme('zIndex.overlay'),\n      width: '100%',\n    },\n  }),\n}\n";