export default "import path from 'path'\nconst btnPrimary = require('components/snippets/classes/btn-primary').default\nconst cardDefaultTooltip = require('components/snippets/classes/card-default-tooltip').default\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"list list-2 items-center\" data-xt-tooltip=\"{ elements: ':scope > a, :scope > button', targets: ':scope > .tooltip' }\">\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n    tooltip\n  </button>\n\n  <div class=\"tooltip transform transition duration-200 opacity-0 translate-y-2 active:opacity-100 active:translate-y-0\">\n    <div class=\"tooltip-md rounded shadow-tooltip ${cardDefaultTooltip()}\">\n      Lorem ipsum dolor sit amet\n    </div>\n  </div>\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n    tooltip\n  </button>\n\n  <div class=\"tooltip transform transition duration-300 opacity-0 translate-y-2 active:opacity-100 active:translate-y-0\">\n    <div class=\"tooltip-md rounded shadow-tooltip ${cardDefaultTooltip()}\">\n      Consectetur adipiscing elit\n    </div>\n  </div>\n\n</div>\n`\n";