export default "import path from 'path'\nconst btnPrimary = require('components/snippets/classes/btn-primary').default\nconst btnDefaultDrop = require('components/snippets/classes/btn-default-drop').default\nconst cardDefaultDrop = require('components/snippets/classes/card-default-drop').default\nconst cardDefaultDropList = require('components/snippets/classes/card-default-drop-list').default\nconst iconX = require('components/snippets/icons').iconX\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"list list-2 items-center\">\n\n  <div class=\"demo--drop-animation-css\" data-xt-drop>\n\n    <button type=\"button\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n      list\n    </button>\n\n    <div class=\"drop group\">\n      <div class=\"w-64 py-3 rounded-md shadow-drop ${cardDefaultDropList()}\">\n        <nav class=\"list flex-col\">\n          <a href=\"#\" class=\"btn btn-sm ${btnDefaultDrop()}\">\n            Lorem ipsum dolor sit amet, consectetur adipiscing elit\n          </a>\n          <button type=\"button\" class=\"btn btn-sm ${btnDefaultDrop()}\">\n            Dolor sit\n          </button>\n          <button type=\"button\" class=\"btn btn-sm ${btnDefaultDrop()}\">\n            Amet\n          </button>\n        </nav>\n      </div>\n    </div>\n\n  </div>\n\n  <div class=\"demo--drop-animation-css\" data-xt-drop>\n\n    <button type=\"button\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n      card\n    </button>\n\n    <div class=\"drop\">\n      <div class=\"card w-80 rounded-md shadow-drop ${cardDefaultDrop()}\">\n        <div class=\"btn btn-close p-5 text-xl\" aria-label=\"Close\">\n          ${iconX()}\n        </div>\n        <div class=\"card-md\">\n          <div class=\"h4\">Lorem ipsum</div>\n          <p><strong>Lorem ipsum</strong> dolor sit amet, <a href=\"#\">consectetur adipiscing</a> elit. Nullam suscipit, velit eu tristique mollis, dui felis dictum turpis, a auctor est odio ac diam. Sed mauris augue, sagittis vitae magna eget, vehicula scelerisque elit.</p>\n        </div>\n      </div>\n    </div>\n\n  </div>\n\n</div>\n`\n";