export default "const round = num =>\n  num\n    .toFixed(7)\n    .replace(/(\\.[0-9]+?)0+$/, '$1')\n    .replace(/\\.0$/, '')\nconst rem = px => `${round(px / 16)}rem`\n\nmodule.exports = {\n  variants: ['responsive'],\n  utility: {\n    '.card-sm': {\n      // styles\n      '@apply text-sm': '',\n      padding: `${rem(20)} ${rem(25)}`,\n    },\n    '.card-md': {\n      // styles\n      '@apply text-base': '',\n      padding: `${rem(30)} ${rem(35)}`,\n    },\n    '.card-lg': {\n      // styles\n      '@apply text-lg': '',\n      padding: `${rem(40)} ${rem(45)}`,\n    },\n    '.card-group': {\n      // setup\n      display: 'flex',\n      alignItems: 'inherit',\n    },\n    '.card-disable': {\n      // setup\n      '@apply text-black links-default !important': '',\n      borderWidth: '0 !important',\n      backgroundColor: 'transparent !important',\n      boxShadow: 'none !important',\n      '> .design-setup': {\n        display: 'none !important',\n      },\n      '&, > *, > * > *': {\n        padding: '0 !important',\n      },\n      '> .btn-close': {\n        display: 'none !important',\n      },\n    },\n  },\n  component: {\n    '.card': {\n      // setup\n      position: 'relative',\n      display: 'flex',\n      flexDirection: 'column',\n      alignItems: 'stretch',\n      justifyContent: 'flex-start',\n      flex: '1 1 auto',\n      width: '100%',\n    },\n  },\n}\n";