export default "const round = num =>\n  num\n    .toFixed(7)\n    .replace(/(\\.[0-9]+?)0+$/, '$1')\n    .replace(/\\.0$/, '')\nconst rem = px => `${round(px / 16)}rem`\n\nmodule.exports = {\n  variants: ['responsive'],\n  utility: theme => ({\n    '.h1': {\n      // style\n      marginTop: theme('spacing.5'),\n      marginBottom: theme('spacing.3'),\n      '@apply mt-fc mb-lc': {},\n      fontFamily: theme('fontFamily.sans').toString(),\n      fontWeight: theme('fontWeight.bold'),\n      fontSize: rem(30),\n      lineHeight: 1.1,\n      letterSpacing: theme('letterSpacing.tight'),\n      textTransform: 'none',\n      '@screen md': {\n        fontSize: rem(40),\n      },\n    },\n    '.h1-display': {\n      // style\n      fontSize: rem(35),\n      '@screen sm': {\n        fontSize: rem(50),\n      },\n      '@screen md': {\n        fontSize: rem(55),\n      },\n      '@screen lg': {\n        fontSize: rem(60),\n      },\n    },\n    '.h2': {\n      // style\n      marginTop: theme('spacing.5'),\n      marginBottom: theme('spacing.3'),\n      '@apply mt-fc mb-lc': {},\n      fontFamily: theme('fontFamily.sans').toString(),\n      fontWeight: theme('fontWeight.bold'),\n      fontSize: rem(25),\n      lineHeight: 1.2,\n      letterSpacing: theme('letterSpacing.tight'),\n      textTransform: 'none',\n      '@screen md': {\n        fontSize: rem(35),\n      },\n    },\n    '.h3': {\n      // style\n      marginTop: theme('spacing.5'),\n      marginBottom: theme('spacing.3'),\n      '@apply mt-fc mb-lc': {},\n      fontFamily: theme('fontFamily.sans').toString(),\n      fontWeight: theme('fontWeight.bold'),\n      fontSize: rem(20),\n      lineHeight: 1.2,\n      letterSpacing: theme('letterSpacing.tight'),\n      textTransform: 'none',\n      '@screen md': {\n        fontSize: rem(30),\n      },\n    },\n    '.h4': {\n      // style\n      marginTop: theme('spacing.5'),\n      marginBottom: theme('spacing.3'),\n      '@apply mt-fc mb-lc': {},\n      fontFamily: theme('fontFamily.sans').toString(),\n      fontWeight: theme('fontWeight.bold'),\n      fontSize: rem(19),\n      lineHeight: 1.3,\n      letterSpacing: theme('letterSpacing.tight'),\n      textTransform: 'none',\n      '@screen md': {\n        fontSize: rem(24),\n      },\n    },\n    '.h5': {\n      // style\n      marginTop: theme('spacing.5'),\n      marginBottom: theme('spacing.3'),\n      '@apply mt-fc mb-lc': {},\n      fontFamily: theme('fontFamily.sans').toString(),\n      fontWeight: theme('fontWeight.bold'),\n      fontSize: rem(15),\n      lineHeight: 1.3,\n      letterSpacing: theme('letterSpacing.tight'),\n      textTransform: 'none',\n      '@screen md': {\n        fontSize: rem(18),\n      },\n    },\n    '.h6': {\n      // style\n      marginTop: theme('spacing.5'),\n      marginBottom: theme('spacing.3'),\n      '@apply mt-fc mb-lc': {},\n      fontFamily: theme('fontFamily.sans').toString(),\n      fontWeight: theme('fontWeight.bold'),\n      fontSize: '14px',\n      lineHeight: 1.3,\n      letterSpacing: theme('letterSpacing.wide'),\n      textTransform: 'uppercase',\n    },\n    '.h-block': {\n      // style\n      marginTop: theme('spacing.6'),\n      marginBottom: theme('spacing.6'),\n      '@apply mt-fc mb-lc': {},\n      paddingTop: theme('spacing.3'),\n      paddingBottom: theme('spacing.3'),\n      paddingLeft: theme('spacing.6'),\n      paddingRight: theme('spacing.6'),\n    },\n    '.p': {\n      // setup\n      fontFamily: 'inherit',\n      fontStyle: 'inherit',\n      fontWeight: 'inherit',\n      letterSpacing: 'inherit',\n      textTransform: 'inherit',\n      // style\n      marginBottom: theme('spacing.3'),\n      '@apply text-reset mb-lc': {},\n    },\n    '.text-reset': {\n      // setup\n      fontFamily: 'inherit',\n      fontStyle: 'inherit',\n      fontWeight: 'inherit',\n      fontSize: 'inherit',\n      lineHeight: 'inherit',\n      letterSpacing: 'inherit',\n      textTransform: 'inherit',\n    },\n    '.ellipsis': {\n      // setup\n      overflow: 'hidden',\n      whiteSpace: 'nowrap',\n      textOverflow: 'ellipsis',\n      '> *': {\n        display: 'inline !important',\n        verticalAlign: 'middle !important',\n      },\n    },\n    '.lowercase-capitalize': {\n      // setup\n      textTransform: 'lowercase',\n      '&:first-letter': {\n        textTransform: 'uppercase',\n      },\n    },\n  }),\n  component: theme => ({\n    html: {\n      // base rem value\n      fontSize: rem(14),\n      '@screen sm': {\n        fontSize: rem(15.5), // odd number with .5 to fix chrome alignment\n      },\n      '@screen md': {\n        fontSize: rem(16),\n      },\n      '@screen xl': {\n        fontSize: rem(17.5), // odd number with .5 to fix chrome alignment\n      },\n      // style\n      '@apply text-black links-default': '',\n      lineHeight: 1.8,\n    },\n    body: {\n      // style\n      '@apply text-base': '',\n    },\n    h1: {\n      // setup\n      '@apply h1': '',\n    },\n    h2: {\n      // setup\n      '@apply h2': '',\n    },\n    h3: {\n      // setup\n      '@apply h3': '',\n    },\n    h4: {\n      // setup\n      '@apply h4': '',\n    },\n    h5: {\n      // setup\n      '@apply h5': '',\n    },\n    h6: {\n      // setup\n      '@apply h6': '',\n    },\n    p: {\n      // setup\n      '@apply p': '',\n      // @FIX NESTED APPLY\n      '@apply mb-lc': {},\n    },\n    strong: {\n      // styles\n      fontWeight: theme('fontWeight.semibold'),\n    },\n    blockquote: {\n      // styles\n      marginBottom: theme('spacing.3'),\n      '@apply mb-lc': {},\n    },\n    code: {\n      // styles\n      fontSize: '0.85em',\n      color: theme('colors.accent.600'),\n    },\n    '.links-inverse': {\n      // styles\n      code: {\n        color: theme('colors.white'),\n        opacity: '0.8',\n      },\n    },\n    'ul:not([class]), ol:not([class])': {\n      // style\n      marginBottom: theme('spacing.3'),\n      '@apply mb-lc': {},\n      '> li': {\n        position: 'relative',\n        marginBottom: theme('spacing.3'),\n        '@apply mb-lc': {},\n        '&:before': {\n          position: 'absolute',\n          display: 'inline-block',\n          left: '0',\n        },\n        '> ul:not([class]), > ol:not([class])': {\n          marginTop: theme('spacing.3'),\n        },\n      },\n    },\n    'ul:not([class])': {\n      // style\n      '> li': {\n        paddingLeft: '1.25rem',\n        '&:before': {\n          content: '\"\\\\2022\"',\n          color: theme('colors.gray.600'),\n        },\n      },\n    },\n    'ol:not([class])': {\n      // style\n      '> li': {\n        counterIncrement: 'ol-counter',\n        paddingLeft: '1.5rem',\n        '&:before': {\n          content: 'counter(ol-counter) \".\"',\n          color: theme('colors.gray.600'),\n        },\n      },\n    },\n    dl: {\n      // style\n      marginBottom: theme('spacing.3'),\n      '@apply mb-lc': {},\n    },\n    hr: {\n      // style\n      marginTop: theme('spacing.6'),\n      marginBottom: theme('spacing.6'),\n      '@apply mt-fc mb-lc': {},\n    },\n    'figure, .figure': {\n      // style\n      marginTop: theme('spacing.6'),\n      marginBottom: theme('spacing.6'),\n      '@apply mt-fc mb-lc': {},\n    },\n    'figcaption, .figcaption': {\n      // style\n      marginTop: theme('spacing.3'),\n      fontSize: rem(14),\n      lineHeight: theme('lineHeight.snug'),\n    },\n  }),\n}\n";