export default "#iframe--furniture-layout-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /**\n      * .featured\n      */\n\n    .featured {\n      .padding(y, huge);\n      background: @white;\n    }\n\n    .featured_item {\n      .full();\n      .margin(y, huge);\n      .text-black links-default() !important;\n    }\n\n    .featured--inverse {\n      background: @shade-700;\n      .featured_item {\n        .text-white links-inverse() !important;\n      }\n    }\n\n    .featured_item_inner {\n      > .row {\n        align-items: center;\n      }\n    }\n\n    .featured_img_col {\n      z-index: 1;\n      .col(12);\n      .col-custom(40%, sm);\n    }\n\n    .featured_item--big {\n      .featured_img_col {\n        .col-custom(55%, sm);\n      }\n    }\n\n    .featured_content_col {\n      z-index: 2;\n      .col(12);\n      .col-auto(sm);\n    }\n\n    .featured_img {\n      .media-container();\n      padding-bottom: 72.6%;\n      overflow: hidden;\n    }\n\n    .featured_img--vertical {\n      padding-bottom: 121%;\n    }\n\n    .featured_img_inner {\n      .media-inner();\n      img {\n        .media();\n        .object-cover();\n      }\n    }\n\n    .featured_content {\n      @media @min-sm {\n        .padding(x, giant);\n      }\n    }\n\n    .featured_item--align-right {\n      .featured_content {\n        @media @min-sm {\n          text-align: right;\n        }\n      }\n    }\n\n    .featured_title {\n      .spacing-md();\n      font-size: 4rem;\n      @media @min-sm {\n        .margin-remove(left, 12rem);\n      }\n    }\n\n    .featured_desc {\n      font-size: 1rem;\n      line-height: 2.5;\n    }\n\n    .featured_item--desc-right {\n      .featured_desc {\n        @media @min-sm {\n          .padding(left, 6rem);\n          .margin-remove(right, 18rem);\n        }\n      }\n    }\n\n    .featured_item--right {\n      .featured_img_col {\n        .order(2, sm);\n      }\n      .featured_content_col {\n        .order(1, sm);\n      }\n      .featured_title {\n        .margin(left, 0);\n        .margin-remove(right, 12rem);\n      }\n    }\n\n    /**\n      * .btn--line\n      */\n\n    @line-width: 15px;\n    @line-space: 15px;\n    @line-opacity: 0.4;\n\n    .btn--line {\n      .padding(x, none);\n      .font-sans-semibold();\n      font-size: 12px;\n      letter-spacing: .1em;\n      text-transform: uppercase;\n      border-color: transparent;\n      background: transparent;\n      color: @text;\n      &:hover {\n        color: @text;\n        box-shadow: none;\n      }\n      &:active, &.active {\n        border-color: transparent;\n        background: transparent;\n        box-shadow: none;\n        transform: none;\n      }\n    }\n\n    .btn--line_text {\n      .padding(left, @line-width + @line-space);\n    }\n\n    .btn--line_line {\n      position: absolute;\n      width: @line-width;\n      height: 1px;\n      background: @text;\n      opacity: @line-opacity;\n    }\n\n    .featured--inverse {\n      .btn--line {\n        color: @text-white links-inverse;\n        &:hover {\n          color: @text-white links-inverse;\n          box-shadow: none;\n        }\n      }\n      .btn--line_line {\n        background: @white;\n      }\n    }\n\n  }\n  /*##END*/\n}\n";