export default "import React from 'react'\nimport path from 'path'\nconst cardStickyDefault = require('components/snippets/classes/card-sticky-default').default\nconst cardStickyPrimary = require('components/snippets/classes/card-sticky-primary').default\n\nimport DemoVanillaIframe from 'components/demo/demo-vanilla-iframe'\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  container: false,\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<br><br><br><br><br><br><br>\n\n<div class=\"sticky-0\" data-xt-sticky=\"{ limit: { top: 0, bottom: '.sticky-1.xt-clone' } }\"> <!-- if limit to sticky use .xt-clone -->\n  <div class=\"card ${cardStickyPrimary()}\">\n    <div class=\"h4\">Sticky</div>\n  </div>\n</div>\n\n<div class=\"card ${cardStickyDefault()}\">\n  <div class=\"h4\">Lorem ipsum</div>\n  <p><strong>Lorem ipsum</strong> dolor sit amet, <a href=\"#\">consectetur adipiscing</a> elit. Nullam suscipit, velit eu tristique mollis, dui felis dictum turpis, a auctor est odio ac diam. Sed mauris augue, sagittis vitae magna eget, vehicula scelerisque elit.</p>\n</div>\n\n<div class=\"sticky-1\" data-xt-sticky=\"{ position: 'bottom', limit: { bottom: 50 } }\">\n  <div class=\"card ${cardStickyPrimary()}\">\n    <div class=\"h4\">Sticky</div>\n  </div>\n</div>\n\n<div class=\"card ${cardStickyDefault()}\">\n  <div class=\"h4\">Lorem ipsum</div>\n  <p><strong>Lorem ipsum</strong> dolor sit amet, <a href=\"#\">consectetur adipiscing</a> elit. Nullam suscipit, velit eu tristique mollis, dui felis dictum turpis, a auctor est odio ac diam. Sed mauris augue, sagittis vitae magna eget, vehicula scelerisque elit.</p>\n</div>\n\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n<br><br><br><br><br><br><br>\n`\n\nclass Page extends React.Component {\n  render() {\n    return <DemoVanillaIframe demo={demo} />\n  }\n}\n\nexport default Page\n";