export default "module.exports = {\n  component: {\n    '.icon': {\n      // setup\n      display: 'inline-block',\n      stroke: 'currentColor',\n      // styles\n      '@apply icon-md': '',\n    },\n    '.icon-sm': {\n      // styles\n      width: '1em',\n      height: '1em',\n    },\n    '.icon-md': {\n      // styles\n      width: '1.25em',\n      height: '1.25em',\n      // Size bigger than `1em` has negative margin\n      margin: 'calc((1em - 1.25em) / 2)',\n    },\n    '.icon-lg': {\n      // styles\n      width: '1.5em',\n      height: '1.5em',\n      // Size bigger than `1em` has negative margin\n      margin: 'calc((1em - 1.5em) / 2)',\n    },\n  },\n}\n";