export default "import path from 'path'\nconst btnPrimary = require('components/snippets/classes/btn-primary').default\nconst cardDefaultTooltip = require('components/snippets/classes/card-default-tooltip').default\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"list list-2 items-center\">\n\n  <div class=\"inline-block\" data-xt-tooltip=\"{ elements: false, elementsInner: ':scope > a, :scope > button', on: 'mouseenter', off: 'mouseleave', closeOutside: false, preventEvent: true }\">\n\n    <a href=\"#tooltip-with-link-1\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n      tooltip\n    </a>\n\n    <div class=\"tooltip\">\n      <div class=\"tooltip-md rounded shadow-tooltip ${cardDefaultTooltip()}\">\n        Lorem ipsum dolor sit amet\n      </div>\n    </div>\n\n  </div>\n\n</div>\n`\n";