export default "const round = num =>\n  num\n    .toFixed(7)\n    .replace(/(\\.[0-9]+?)0+$/, '$1')\n    .replace(/\\.0$/, '')\nconst rem = px => `${round(px / 16)}rem`\n\nmodule.exports = {\n  variants: ['responsive'],\n  utility: {\n    '.slider-disable': {\n      // setup\n      '@apply xt-disable-after-init': '',\n      '.slider-pagination, [data-xt-nav]': {\n        display: 'none !important',\n      },\n      '.xt-wrap': {\n        visibility: 'hidden !important',\n        opacity: '0 !important',\n      },\n      // @FIX do calculation first\n      '&.xt-disabled': {\n        '.slide:not(.in)': {\n          visibility: 'hidden !important',\n          opacity: '0 !important',\n          height: '0 !important',\n        },\n      },\n    },\n    '.slider-expand': {\n      // setup\n      '@apply xt-disable': '',\n      '.slides': {\n        overflow: 'visible',\n        height: 'auto !important',\n      },\n      '.slides-inner': {\n        '@apply duration-none transform-none !important': '',\n        flexWrap: 'wrap !important',\n      },\n      '.slide:not(.in)': {\n        opacity: '1 !important',\n      },\n      '.xt-wrap, .slider-pagination, [data-xt-nav], .loader, .filler, .spinner': {\n        display: 'none !important',\n      },\n    },\n  },\n  component: theme => ({\n    '.slider': {\n      // setup\n      position: 'relative',\n      overflow: 'hidden',\n    },\n    '.slides': {\n      // setup\n      position: 'relative',\n      display: 'inline-block', // @FIX autoheight values\n      verticalAlign: 'top', // @FIX inline-block margin below\n      width: '100%',\n      // styles\n      transitionProperty: 'height',\n      transitionDuration: theme('transitionDuration.500'),\n      transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n    },\n    '.slide': {\n      // setup\n      position: 'relative',\n    },\n    '.slide-absolute': {\n      // setup\n      position: 'relative',\n      display: 'none',\n      '&.in': {\n        display: 'block',\n        zIndex: theme('zIndex.active'),\n      },\n      '&.out': {\n        display: 'block',\n      },\n      '&.next': {\n        display: 'block',\n        zIndex: `calc(${theme('zIndex.active')} + 1)`,\n      },\n      '&:not(.in)': {\n        position: 'absolute',\n        top: '0',\n        left: '0',\n      },\n      // @FIX page load height without .active slides\n      '&:not([data-xt-component=\"xt-slider\"])': {\n        '.slide': {\n          '&:first-child': {\n            display: 'block !important',\n            position: 'relative !important',\n          },\n        },\n      },\n    },\n    '.slides-inner': {\n      // styles\n      flexWrap: 'nowrap',\n      alignItems: 'flex-start',\n      // animation dragger\n      transitionProperty: 'transform',\n      transitionDuration: theme('transitionDuration.500'),\n      transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n    },\n    '.slider-pagination, .slider-navigation': {\n      // styles\n      paddingTop: theme('spacing.4'),\n      alignItems: 'center',\n      justifyContent: 'center',\n    },\n    '[data-xt-pag].btn': {\n      // styles\n      minHeight: '1.3rem',\n      borderRadius: '2rem',\n      padding: `${rem(7)} ${rem(11)}`,\n      fontSize: rem(10),\n      '&.active': {\n        padding: `${rem(7)} ${rem(20)}`,\n      },\n      // animation\n      transitionProperty: 'all',\n      transitionDuration: theme('transitionDuration.500'),\n      transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n    },\n    '[data-xt-nav].btn': {\n      minHeight: '1.3rem',\n      borderRadius: '2rem',\n      padding: `${rem(7)} ${rem(11)}`,\n      fontSize: rem(10),\n    },\n    '.xt-overflow-auto': {\n      '@apply xt-disable': '',\n      '.slider-pagination, [data-xt-nav], .xt-wrap': {\n        display: 'none !important',\n      },\n      // @FIX disable slider if not overflowing\n      '.slides-inner': {\n        '@apply duration-none transform-none !important': '',\n      },\n    },\n  }),\n}\n";