export default ".demo--slider-progress {\n  > .loader {\n    /* touch */\n    @media (hover: none) {\n      transform: none !important;\n      position: absolute;\n      overflow: visible;\n      top: auto;\n      right: auto;\n      left: 0;\n      bottom: 12px;\n      margin: 0;\n      .spinner {\n        opacity: 1;\n      }\n    }\n    /* mouse */\n    @media (hover: hover) {\n      @apply mouse-follow overflow-visible;\n      .spinner {\n        @apply -mt-1 ml-4;\n      }\n    }\n  }\n}\n";