export default ".demo--scroll-responsive {\n  .card {\n    @media (max-width: theme('screens.md')) {\n      /* mode-infinite */\n      /* initial */\n      @apply transform transition;\n      @apply opacity-0 -translate-x-4;\n      &.inverse {\n        @apply translate-x-4;\n      }\n      /* active */\n      &.fade-active {\n        @apply opacity-100 translate-x-0;\n      }\n      /* out */\n      &.fade-out {\n        @apply opacity-0 translate-x-4;\n        &.inverse {\n          @apply -translate-x-4;\n        }\n      }\n    }\n    @screen md {\n      /* mode-once */\n      &:not(.scroll-done) {\n        /* initial */\n        @apply transform transition;\n        @apply opacity-0 -translate-x-4;\n        &.inverse {\n          @apply translate-x-4;\n        }\n        /* active */\n        &.fade-active {\n          @apply opacity-100 translate-x-0;\n        }\n        /* out */\n        &.fade-out {\n          @apply opacity-0 translate-x-4;\n          &.inverse {\n            @apply -translate-x-4;\n          }\n        }\n      }\n    }\n  }\n}\n";