export default "import { Xt } from 'xtendui'\n\nXt.mount.push({\n  matches: '.demo--scroll-direction',\n  mount: object => {\n    // init\n\n    let self = new Xt.Scroll(object, {\n      elements: '.card',\n      delayOn: current => {\n        return Math.min((current + 1) * 75, 300)\n      },\n      delayOff: (current, total) => {\n        return Math.min((total - (current + 1)) * 75, 300)\n      },\n    })\n\n    // unmount\n\n    return () => {\n      self.destroy()\n      self = null\n    }\n  },\n})\n";