export default ".demo--overlay-animation-css {\n  @apply duration-500; /* needed for animation time, put max time with delay */\n  /* initial */\n  .overlay-inner {\n    @apply transform transition;\n    @apply opacity-0 -translate-x-4;\n  }\n  /* active */\n  &.active {\n    .overlay-inner {\n      @apply opacity-100 translate-x-0;\n    }\n  }\n  /* out */\n  &.out {\n    .overlay-inner {\n      @apply opacity-0 translate-x-4;\n    }\n  }\n}\n";