export default "#iframe--google-locator {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    .google-locator {\n      display: flex;\n      flex-wrap: wrap;\n      flex: 1 1 auto;\n      @media (max-width: theme('screens.md')) {\n        flex-flow: column;\n      }\n      @screen md {\n        min-height: 100vh;\n        min-height: calc(var(--vh, 1vh) * 100);\n      }\n    }\n\n    .google-locator-main {\n      flex: 1 1 auto;\n      position: relative;\n      min-height: 100%;\n      @screen md {\n        position: fixed;\n        top: 0;\n        right: 0;\n        width: 100%;\n        height: 100%;\n        padding-left: 20rem;\n      }\n      @screen lg {\n        padding-left: 25rem;\n      }\n      @screen xl {\n        padding-left: 30rem;\n      }\n      /* hide map on mobile */\n      @media (max-width: theme('screens.md')) {\n        @apply sr-only;\n        height: 100vh;\n        visibility: hidden;\n        pointer-events: none;\n        position: fixed; /* @FIX no absolute or bugs search */\n        z-index: -1;\n        top: -100vh;\n        left: -100vw;\n        opacity: 0;\n      }\n    }\n\n    .google-locator-main-inner {\n      position: relative;\n      width: 100%;\n      height: 100%;\n    }\n\n    .google-locator-main-map {\n      width: 100%;\n      height: 100%;\n    }\n\n    .google-locator-aside {\n      position: relative;\n      z-index: 1;\n      @screen md {\n        width: 20rem;\n        min-height: 100%;\n        box-shadow: 0 0 0.7rem rgba(0, 0, 0, 0.2);\n      }\n      @screen lg {\n        width: 25rem;\n      }\n      @screen xl {\n        width: 30rem;\n      }\n    }\n\n    .google-locator-aside-head {\n      position: relative;\n      padding: 2rem;\n      border-bottom: 1px solid theme('colors.gray.200');\n    }\n\n    .google-locator-result {\n      @apply text-xs;\n      padding-bottom: 0.7rem;\n      border-bottom: 1px solid theme('colors.gray.200');\n    }\n\n    .google-locator-aside-body {\n      position: relative;\n      padding: 2rem;\n      .google-locator-result {\n        display: none;\n      }\n      .google-locator-result-initial {\n        display: block;\n      }\n      &.found {\n        .google-locator-result {\n          display: none;\n        }\n        .google-locator-result-founds {\n          display: block;\n        }\n      }\n      &.noplace {\n        .google-locator-result {\n          display: none;\n        }\n        .google-locator-result-noplace {\n          display: block;\n        }\n      }\n      &.empty {\n        .google-locator-result {\n          display: none;\n        }\n        .google-locator-result-empty {\n          display: block;\n        }\n      }\n      &.error {\n        .google-locator-result {\n          display: none;\n        }\n        .google-locator-result-error {\n          display: block;\n        }\n      }\n    }\n\n    .btn-repeat {\n      position: absolute;\n      z-index: 2;\n      top: 2rem;\n      left: 2rem;\n    }\n\n    .google-locator-items {\n      @apply mt-4;\n    }\n\n    .google-locator-item {\n      @apply py-2;\n      > .list {\n        @apply list-2;\n        flex-wrap: nowrap;\n        align-items: center;\n        justify-content: space-between;\n      }\n    }\n\n    .google-locator-items .google-locator-item {\n      /* activation when clicking marker */\n      cursor: pointer;\n      border-bottom: 1px solid theme('colors.gray.200');\n      @apply transition;\n      &.active {\n        border-bottom: 1px solid theme('colors.accent.500');\n      }\n    }\n\n    .google-locator-item-name {\n      @apply h5;\n    }\n\n    .google-locator-item-address {\n      @apply text-sm;\n      font-style: normal;\n      text-transform: uppercase;\n    }\n\n    .google-locator-item-additional {\n      @apply text-sm;\n      text-transform: uppercase;\n    }\n\n    .google-locator-item-directions {\n      @apply text-sm;\n      text-transform: uppercase;\n    }\n\n    /* infoWindow */\n\n    .gm-style {\n      /* reset */\n      .gm-style-iw-d {\n        overflow: visible !important;\n        max-width: none !important;\n        max-height: none !important;\n      }\n      /* arrow */\n      .gm-style-iw-t {\n        &:after {\n          background: theme('colors.white');\n        }\n      }\n      /* box */\n      .gm-style-iw-c {\n        @apply p-4 !important;\n        border: 0;\n        border-radius: 0;\n        //box-shadow: @shadow-light;\n        background: theme('colors.white');\n        /* scroll */\n        @apply overflow-sub;\n        overflow-x: hidden !important;\n        overflow-y: auto !important;\n        max-height: 15rem !important;\n      }\n      /* close */\n      .gm-style-iw-d + button {\n        top: 0 !important;\n        right: 0 !important;\n        width: 3rem !important;\n        height: 3rem !important;\n        background: theme('colors.gray.200') !important;\n        border-bottom-left-radius: 0.7rem;\n        font-size: 25px;\n        color: theme('colors.black') !important;\n        line-height: 0;\n        text-align: center;\n        opacity: 1 !important;\n        &:before {\n          content: '×';\n          position: relative;\n          top: -2px;\n        }\n        img {\n          display: none !important;\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";