export default "import path from 'path'\nconst btnPrimary = require('components/snippets/classes/btn-primary').default\nconst btnDefaultDrop = require('components/snippets/classes/btn-default-drop').default\nconst cardDefaultDropList = require('components/snippets/classes/card-default-drop-list').default\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"inline-block\" data-xt-drop=\"{ elements: false, elementsInner: ':scope > a, :scope > button' }\">\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n    drop\n  </button>\n\n  <div class=\"drop\">\n    <div class=\"w-64 py-3 rounded-md shadow-drop ${cardDefaultDropList()}\">\n      <nav class=\"list flex-col\">\n        <a href=\"#\" class=\"btn btn-sm ${btnDefaultDrop()}\">\n          Lorem ipsum dolor sit amet, consectetur adipiscing elit\n        </a>\n        <button type=\"button\" class=\"btn btn-sm ${btnDefaultDrop()}\">\n          Dolor sit\n        </button>\n        <button type=\"button\" class=\"btn btn-sm ${btnDefaultDrop()}\">\n          Amet\n        </button>\n      </nav>\n    </div>\n  </div>\n\n</div>\n`\n";