export default ".jump-to-content {\n  position: fixed !important;\n  left: 0;\n  width: 100%;\n  display: flex;\n  justify-content: center;\n  z-index: theme('zIndex.top');\n  /* initial */\n  @apply transform transition;\n  @apply opacity-0 -translate-y-4;\n  /* active */\n  &.active {\n    @apply opacity-100 translate-y-0;\n  }\n  /* out */\n  &.out {\n    @apply opacity-0 -translate-y-4;\n  }\n}\n";