export default "module.exports = {\n  utility: {\n    '.sticky-disable': {\n      // setup\n      '@apply xt-disable': '',\n      position: 'relative !important',\n      top: 'initial !important',\n      left: 'initial !important',\n      right: 'initial !important',\n      bottom: 'initial !important',\n      '&.xt-clone': {\n        display: 'none !important',\n      },\n      '&:not(.xt-clone)': {\n        position: 'relative !important',\n      },\n    },\n  },\n  component: theme => ({\n    '.xt-sticky': {\n      // setup\n      '&.xt-clone': {\n        pointerEvents: 'none',\n        position: 'relative',\n        visibility: 'hidden',\n        top: '0',\n        width: '100%',\n      },\n      '&:not(.xt-clone)': {\n        position: 'relative',\n        width: '100%',\n      },\n      '&.xt-clone:not(.in)': {\n        position: 'absolute',\n      },\n      '&:not(.xt-clone):not(.in)': {\n        position: 'relative',\n      },\n      '.show-sticky': {\n        display: 'none',\n      },\n      '.hide-sticky': {\n        display: 'block',\n      },\n      '&:not(.xt-clone).active': {\n        position: 'fixed',\n        zIndex: theme('zIndex.sticky'),\n        width: '100%',\n        '.show-sticky': {\n          display: 'block',\n        },\n        '.hide-sticky': {\n          display: 'none',\n        },\n      },\n    },\n    '.xt-sticky-absolute': {\n      // setup\n      '&.xt-clone': {\n        position: 'relative',\n      },\n      '&.xt-clone:not(.in)': {\n        position: 'relative',\n      },\n      '&:not(.xt-clone):not(.in)': {\n        position: 'relative',\n      },\n      '&.xt-fixed:not(.xt-clone):not(.in)': {\n        position: 'absolute',\n        width: '100%',\n      },\n    },\n    '.xt-sticky-fixed': {\n      // setup\n      '&.xt-clone': {\n        position: 'relative',\n      },\n      '&.xt-clone:not(.in)': {\n        position: 'relative',\n      },\n      '&:not(.xt-clone):not(.in)': {\n        position: 'relative',\n      },\n      '&.xt-fixed:not(.xt-clone):not(.in)': {\n        position: 'fixed',\n        zIndex: theme('zIndex.sticky'),\n        width: '100%',\n      },\n    },\n    '.xt-sticky-fixed-always': {\n      // setup\n      '&.xt-clone': {\n        position: 'absolute',\n      },\n      '&.xt-clone:not(.in)': {\n        position: 'absolute',\n      },\n      '&:not(.xt-clone):not(.in)': {\n        position: 'fixed',\n        zIndex: theme('zIndex.sticky'),\n        width: '100%',\n      },\n      '&.xt-fixed:not(.xt-clone):not(.in)': {\n        position: 'fixed',\n        zIndex: theme('zIndex.sticky'),\n        width: '100%',\n      },\n    },\n    '.sticky-moving': {\n      // animation\n      transitionProperty: 'top, bottom, opacity',\n      transitionDuration: theme('transitionDuration.500'),\n      transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n    },\n    '.sticky-hide': {\n      // styles\n      opacity: '0',\n    },\n    '.sticky-disable': {\n      // styles\n      opacity: '1 !important',\n    },\n  }),\n}\n";