export default "import path from 'path'\nconst btnDefault = require('components/snippets/classes/btn-default').default\nconst cardToggle = require('components/snippets/classes/card-toggle').default\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"list list-2 items-center demo--toggle-timing\">\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\">\n    Toggle 0\n  </button>\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\" data-xt-group=\"0\">\n    Group 0\n  </button>\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\" data-xt-group=\"1\">\n    Group 1\n  </button>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\">\n    Target 0\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"0\">\n    Group 0a\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"1\">\n    Group 1a\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"0\">\n    Group 0b\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"1\">\n    Group 1b\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"0\">\n    Group 0c\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"1\">\n    Group 1c\n  </div>\n\n</div>\n\n<div class=\"list list-2 items-center mt-4 demo--toggle-timing--event\">\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\">\n    Toggle 0 hover\n  </button>\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\" data-xt-group=\"0\">\n    Group 0 hover\n  </button>\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\" data-xt-group=\"1\">\n    Group 1 hover\n  </button>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\">\n    Target 0 hover\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"0\">\n    Group 0a hover\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"1\">\n    Group 1a hover\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"0\">\n    Group 0b hover\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"1\">\n    Group 1b hover\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"0\">\n    Group 0c hover\n  </div>\n\n  <div class=\"card toggle rounded-md ${cardToggle()}\" data-xt-group=\"1\">\n    Group 1c hover\n  </div>\n\n</div>\n`\n";