export default "/**\n * demo\n */\n\n#gatsby_body-inner {\n  position: relative;\n  height: 100vh;\n  height: calc(var(--vh, 1vh) * 100);\n}\n\nhtml.gatsby_iframe-inside {\n  body {\n    overflow-x: hidden;\n    overflow-y: scroll;\n    background: theme('colors.white') !important;\n    @apply overflow-main;\n  }\n  #gatsby_body-inner {\n    height: initial;\n    max-width: none;\n  }\n  &.gatsby_iframe-container {\n    #gatsby_body-inner {\n      @apply p-4 md:p-6 lg:p-8;\n    }\n  }\n}\n\n.gatsby_demo_source {\n  @apply relative p-4 md:p-6 lg:p-8;\n}\n\n/**\n * code\n */\n\npre[class*=\"language-\"] {\n  overflow-x: hidden !important;\n  overflow-y: auto !important;\n  @apply overflow-sub my-5 mt-fc mb-lc py-5 px-5;\n  width: 100%;\n  border-radius: 6px;\n  .gatsby_demo_code_body & {\n    border-top-left-radius: 0;\n    border-top-right-radius: 0;\n  }\n  border: 0 !important;\n  line-height: 0 !important;\n  font-size: 1rem;\n  code {\n    width: 100%;\n    background: transparent !important;\n    @apply font-mono;\n    font-size: 0.8125rem !important;\n    line-height: 1.4 !important;\n    white-space: pre-wrap !important;\n  }\n}\n\n/**\n * .demo\n */\n\npre {\n  max-height: 21rem;\n}\n\n.gatsby_demo {\n  @apply my-6 mt-fc mb-lc;\n  border-radius: 6px;\n  pre {\n    border-width: 0 !important;\n  }\n  @apply border-3 border-gray-200;\n  @apply transition;\n  #gatsby_open-full & {\n    border: 0 !important;\n    .gatsby_demo_code_body,\n    .gatsby_demo_item:not(.populated-iframe) {\n      overflow-x: hidden;\n      overflow-y: auto;\n      @apply overflow-main;\n    }\n    .gatsby_demo_item.active-code {\n      overflow-x: hidden;\n      overflow-y: hidden;\n    }\n    .gatsby_demo_tabs_right {\n      > * {\n        opacity: 1 !important;\n        transform: initial !important;\n      }\n    }\n  }\n}\n\n.gatsby_demo_tabs, .gatsby_demo_code_tabs, .gatsby_demo_code_body {\n  @apply bg-gray-200;\n}\n\n.gatsby_demo_tabs, .gatsby_demo_code_tabs {\n  position: relative;\n  display: flex;\n  align-items: flex-start;\n  justify-content: space-between;\n  padding: 7px;\n  .btn {\n    border-width: 3px;\n    padding: .5em .75em;\n    font-size: 10px;\n    @apply text-black links-default text-opacity-50 rounded-md border-transparent;\n    &:hover, &:active, &.active {\n      @apply border-gray-300 bg-white text-opacity-100;\n    }\n  }\n}\n\n.gatsby_demo_tabs_right, .gatsby_demo_code_tabs_right {\n  flex-wrap: nowrap;\n  .btn {\n    padding-top: .6em;\n    padding-bottom: .6em;\n    [class^='icon-'], [class*=' icon-'] {\n      position: relative;\n      top: .05em;\n      font-size: 12px;\n      margin-top: -2px;\n      margin-bottom: -2px;\n    }\n  }\n}\n\n.gatsby_demo_tabs_inner {\n  margin-top: -3px;\n}\n\n.gatsby_demo_text {\n  @apply py-2 px-4 border border-gray-200;\n}\n\n.gatsby_demo_source {\n  &:not(.gatsby_demo_source--from) {\n    display: none !important;\n  }\n}\n\n.gatsby_demo_item {\n  position: relative;\n  .gatsby_demo_item_wrapper {\n    position: relative;\n    #gatsby_open-full & {\n      height: auto !important;\n    }\n  }\n  .gatsby_demo_code {\n    order: 2;\n    #gatsby_open-full & {\n      @apply toggle;\n      order: -1;\n      position: absolute;\n      width: 100%;\n      height: 100%;\n    }\n    .gatsby_demo_code_inner {\n      display: flex;\n      flex-flow: column;\n      height: 100%;\n    }\n    &.active {\n      z-index: 11;\n    }\n  }\n  &[data-iframe] {\n    &.loaded {\n      .gatsby_demo_item_wrapper > .loader {\n        display: none;\n      }\n    }\n    iframe {\n      display: block;\n      width: 100%;\n      height: 0;\n      border: 0;\n      height: 36rem;\n      #gatsby_open-full & {\n        height: auto;\n      }\n    }\n  }\n  &:not(.populated) {\n    .gatsby_demo_code_body {\n      width: 100%;\n      overflow: hidden;\n    }\n  }\n}\n\n.gatsby_demo_code_body {\n  z-index: 1;\n  flex: 1;\n}\n\n[data-gatsby-listing-toggle] + .gatsby_demo {\n  display: none;\n  .gatsby_demo_source_populate {\n    display: inline-flex; /* populate source disable */\n  }\n}\n\n.gatsby_demo, .gatsby_demo_item, .gatsby_demo_item_wrapper, .gatsby_demo_item_wrapper iframe {\n  display: flex;\n  flex-direction: column;\n  #gatsby_open-full & {\n    flex: 1 1 auto;\n    width: 100%;\n  }\n}\n\n.btn-show-code {\n  display: none;\n  #gatsby_open-full & {\n    display: inline-flex;\n  }\n}\n\n.btn-open-iframe {\n  display: none !important;\n  padding-top: 0.4em !important;\n  padding-bottom: 0.4em !important;\n  > span {\n    display: inline-block;\n    position: relative;\n    top: auto !important;\n    margin-top: -.5em;\n    margin-bottom: -.5em;\n  }\n}\n\n#gatsby_open-full {\n  position: fixed;\n  z-index: calc(theme('zIndex.top') + 100);\n  top: 0;\n  left: 0;\n  bottom: 0;\n  right: 0;\n  @apply bg-white;\n  @screen lg {\n    margin-left: 18rem;\n  }\n  @screen xl {\n    margin-left: 20rem;\n  }\n  > .btn-close {\n    @apply text-white links-inverse rounded-md border-accent-500 bg-accent-500 hover:text-opacity-75 hover:border-accent-600 hover:bg-accent-600;\n    top: 0;\n    right: 0;\n    border-radius: 0;\n  }\n  .gatsby_demo_tabs_right {\n    padding-right: 45px;\n  }\n  .btn-open-full {\n    display: none !important;\n  }\n  .btn-open-iframe.gatsby_with-iframe {\n    display: block !important;\n  }\n}\n\n#gatsby_open-full-content {\n  display: flex;\n  height: 100vh;\n  height: calc(var(--vh, 1vh) * 100);\n  pre {\n    max-height: none;\n  }\n}\n\n.gatsby_table {\n  border-radius: 6px;\n  border: 3px solid theme('colors.gray.200');\n}\n\n.gatsby_note {\n  @apply my-5 mt-fc mb-lc text-sm;\n}\n";