export default ".demo--tooltip-animation-css-multiple {\n  @apply duration-500; /* needed for animation time, put max time with delay */\n  .tooltip {\n    /* initial */\n    @apply transform transition duration-300;\n    @apply opacity-0 -translate-x-4;\n    .inverse& {\n      @apply translate-x-4;\n    }\n    /* active */\n    &.active {\n      @apply opacity-100 translate-x-0;\n    }\n    /* out */\n    &.out {\n      @apply opacity-0 translate-x-4;\n      .inverse& {\n        @apply -translate-x-4;\n      }\n    }\n  }\n}\n";