export default "module.exports = {\n  variants: ['responsive'],\n  utility: theme => ({\n    '.flex-full': {\n      // setup\n      flex: '1 1 100%',\n    },\n    '.container-reset': {\n      // setup\n      padding: 0,\n      width: 'auto',\n      maxWidth: 'none',\n    },\n    '.container-y': {\n      // setup\n      ...theme('container.padding'),\n    },\n    '.-container': {\n      // setup\n      ...theme('container.padding'),\n    },\n    '.-container-y': {\n      // setup\n      ...theme('container.padding'),\n    },\n    '.h-screen': {\n      // setup\n      height: 'calc(var(--vh, 1vh) * 100) !important',\n    },\n    '.min-h-screen': {\n      // setup\n      minHeight: 'calc(var(--vh, 1vh) * 100) !important',\n    },\n    '.max-h-screen': {\n      // setup\n      maxHeight: 'calc(var(--vh, 1vh) * 100) !important',\n    },\n    '.mt-fc': {\n      // setup\n      '&:first-child': {\n        marginTop: theme('spacing.0'),\n      },\n    },\n    '.mb-lc': {\n      // setup\n      '&:last-child': {\n        marginBottom: theme('spacing.0'),\n      },\n    },\n  }),\n  component: theme => ({\n    html: {\n      // setup\n      touchAction: 'manipulation', // disable double-tap\n      '-webkit-tap-highlight-color': 'rgba(0, 0, 0, 0)', // disable tap highlight\n      height: '100%',\n    },\n    body: {\n      // setup\n      '@apply overflow-main': '',\n      height: '100%',\n    },\n    '::selection': {\n      // styles\n      backgroundColor: '#00FFDD',\n      color: theme('colors.black'),\n    },\n    '.overflow-main': {\n      // styles\n      '-webkit-overflow-scrolling': 'touch',\n      '&::-webkit-scrollbar': {\n        height: '10px',\n        width: '10px',\n        background: theme('colors.white'),\n        '@media (hover: none)': {\n          display: 'none',\n        },\n      },\n      '&::-webkit-scrollbar-thumb': {\n        background: theme('colors.gray.600'),\n      },\n      scrollbarWidth: 'auto',\n      scrollbarColor: `${theme('colors.gray.600')} ${theme('colors.white')}`,\n    },\n    '.overflow-sub': {\n      // styles\n      '-webkit-overflow-scrolling': 'touch',\n      '&::-webkit-scrollbar': {\n        height: '5px',\n        width: '5px',\n        background: 'transparent',\n      },\n      '&::-webkit-scrollbar-thumb': {\n        background: theme('colors.gray.600'),\n      },\n      scrollbarWidth: 'thin',\n      scrollbarColor: `${theme('colors.gray.600')} transparent`,\n    },\n    '[readonly], [disabled], .disabled': {\n      // setup\n      cursor: 'not-allowed',\n      pointerEvents: 'none',\n      // styles\n      '&:not(.active)': {\n        // when not .active\n        opacity: '.75',\n      },\n    },\n    '.backdrop': {\n      // setup\n      position: 'fixed',\n      zIndex: theme('zIndex.backdrop'),\n      top: '0',\n      right: '0',\n      width: '100%',\n      height: '100%',\n      // styles\n      backgroundColor: theme('colors.gray.900'),\n      opacity: '0',\n      // animation\n      transitionProperty: 'opacity',\n      transitionDuration: theme('transitionDuration.500'),\n      transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n      '.active &': {\n        opacity: '.3',\n      },\n    },\n    'html:not(.xt-focus-disable)': {\n      ':focus': {\n        outline: 'none !important',\n      },\n      '&.xt-focus-visible': {\n        ':focus': {\n          outline: `5px auto ${theme('colors.accent.700')} !important`,\n        },\n        'input:not([type=\"checkbox\"]):not([type=\"radio\"]), select, textarea': {\n          '&:focus': {\n            outline: 'none !important',\n          },\n        },\n      },\n    },\n    '.design-setup': {\n      position: 'absolute',\n      zIndex: '-1',\n      top: '0',\n      left: '0',\n      bottom: '0',\n      right: '0',\n    },\n  }),\n}\n";