export default "import path from 'path'\nconst btnDefault = require('components/snippets/classes/btn-default').default\nconst cardDefault = require('components/snippets/classes/card-default').default\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"list list-2 items-center justify-center\">\n\n  <div class=\"card rounded-md ${cardDefault()}\">\n    <div class=\"card-sm\">\n      <div class=\"h5\">Small</div>\n      <p><strong>Lorem ipsum</strong> dolor sit amet, <a href=\"#\">consectetur adipiscing</a> elit. Nullam suscipit, velit eu tristique mollis, dui felis dictum turpis, a auctor est odio ac diam. Sed mauris augue, sagittis vitae magna eget, vehicula scelerisque elit.</p>\n    </div>\n    <span class=\"loader loader-x loader-x-animated loader-size-x bg-white bg-opacity-75\">\n      <span class=\"filler text-accent-500\">\n        <span class=\"bg-current\"></span>\n        <span class=\"bg-current opacity-25\"></span>\n      </span>\n    </span>\n  </div>\n\n  <button type=\"button\" class=\"btn btn-md rounded-md ${btnDefault()}\">\n    Lorem ipsum\n    <span class=\"loader loader-x loader-x-animated loader-size-x bg-white bg-opacity-75\">\n      <span class=\"filler text-accent-500\">\n        <span class=\"bg-current\"></span>\n        <span class=\"bg-current opacity-25\"></span>\n      </span>\n    </span>\n  </button>\n\n</div>\n`\n";