export default "#iframe--fashion-gallery-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    .product-gallery {\n      @apply overflow-y-auto overflow-x-hidden overflow-sub -container md:mx-0;\n      @media (max-width: theme('screens.md')) {\n        height: 65vh;\n        height: calc(var(--vh, 1vh) * 65);\n      }\n    }\n\n    .product-dot {\n      > * {\n        width: 7px;\n        height: 7px;\n        @apply transition-all;\n      }\n      &:active, &.active {\n        > * {\n          width: 9px;\n          height: 9px;\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";