export default "@import 'xtendui/src/core/table/_table.less';\n\n/**\n * vars\n */\n\n// generate class: [false], [true] non responsive only, [sm, md, lg] responsive @breakpoints\n\n@generate-table-scroll: sm, md;\n\n/**\n * tables\n */\n\n.table, .table-scroll {\n  .margin(bottom, layout) !important;\n  .margin(bottom, medium);\n}\n\n// .table\n\n.table {\n  th {\n    vertical-align: top;\n    .padding(x, tiny);\n    .padding(y, tiny);\n    .font-sans-semibold();\n    .line-height-sm();\n    font-size: 11px;\n    letter-spacing: .025em;\n    text-transform: uppercase;\n  }\n  td {\n    vertical-align: top;\n    .padding(x, tiny);\n    .padding(y, tiny);\n    .line-height-sm();\n    font-size: 0.8rem;\n  }\n  // thead\n  thead {\n    th, td {\n      border-right: 1px solid @shade-100;\n      border-bottom: 1px solid @shade-100;\n    }\n    th, td {\n      &:last-child {\n        border-right: 0;\n      }\n    }\n  }\n  // tbody\n  tbody {\n    th, td {\n      border-right: 1px solid @shade-100;\n      border-bottom: 1px solid @shade-100;\n    }\n    th, td {\n      &:last-child {\n        border-right: 0;\n      }\n    }\n    tr {\n      &:last-child {\n        th, td {\n          border-bottom: 0;\n        }\n      }\n    }\n  }\n  // tfoot\n  /*\n  tfoot {\n    .padding(y, big) !important;\n    background: @shade-100;\n    th {\n      .reset-text();\n    }\n    tr {\n      border: 0;\n      th, td {\n        vertical-align: top;\n        .padding(y, tiny) !important;\n        .padding(x, medium) !important;\n        text-align: right;\n      }\n      &:first-child {\n        th, td {\n          .padding(top, medium) !important;\n        }\n      }\n      &:last-child {\n        th, td {\n          .padding(bottom, medium) !important;\n        }\n      }\n    }\n  }\n  */\n}\n\n// .table-narrow\n\n.table-narrow {\n  th, td {\n    .padding(x, layout);\n    background: transparent;\n  }\n}\n\n// .table-sm\n\n.table-sm {\n  th {\n    .padding(x, small);\n    .padding(y, small);\n  }\n  td {\n    .padding(x, small);\n    .padding(y, small);\n  }\n}\n\n// .table-stripe\n\n.table-stripe {\n  tbody tr:nth-of-type(odd) {\n    background: @shade-100;\n  }\n  tbody tr:nth-of-type(even) {\n    background: transparent;\n  }\n}\n\n// .table-border\n\n.table-border {\n  border-radius: 6px;\n  border: 3px solid @shade-100;\n  border-collapse: separate;\n  border-spacing: 0;\n}\n\n// .table-responsive\n\n.table-responsive {\n  @media @max-sm {\n    border-top: 2px solid @shade-100;\n    thead, th {\n      display: none;\n    }\n    tr {\n      .full();\n      .padding(y, medium);\n    }\n    td {\n      display: inline-flex;\n      flex-direction: column;\n      justify-content: center;\n      .padding(x, medium);\n      .padding(y, small);\n      text-align: center;\n      .list {\n        justify-content: center;\n      }\n    }\n    // data-th\n    td:before {\n      .form-label();\n      content: attr(data-th);\n      width: 100%;\n      justify-content: center;\n      text-align: center;\n    }\n  }\n}\n";