export default ".demo--toggle-animation-inverse {\n  .card {\n    /* initial */\n    @apply transform transition;\n    @apply opacity-100 translate-x-0;\n    /* active */\n    &.active {\n      @apply opacity-0 translate-x-4;\n      &.inverse {\n        @apply -translate-x-4;\n      }\n    }\n    /* done */\n    &.out {\n      @apply opacity-0 -translate-x-4 transition-none;\n      &.inverse {\n        @apply translate-x-4;\n      }\n    }\n  }\n}\n";