export default "#iframe--infinite-scroll {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    /* .infinite-scroll-spinner */\n\n    .infinite-scroll-spinner {\n      @apply transition opacity-0;\n      .spinner {\n        @apply transition-all opacity-0 scale-75;\n      }\n    }\n\n    .infinite-scroll-loading {\n      .infinite-scroll-spinner {\n        @apply opacity-100;\n        .spinner {\n          @apply opacity-100 scale-100;\n        }\n      }\n    }\n\n    /* .infinite-scroll-trigger .btn */\n\n    .infinite-scroll-trigger .btn {\n      .infinite-scroll-trigger-content-nomore {\n        display: none;\n      }\n      .infinite-scroll-trigger-content {\n        @apply transition;\n      }\n    }\n\n    .infinite-scroll-loading {\n      .infinite-scroll-trigger .btn {\n        pointer-events: none;\n        > .infinite-scroll-trigger-content {\n          @apply opacity-0;\n        }\n      }\n    }\n\n    /* .infinite-scroll-nomore */\n\n    .infinite-scroll-nomore {\n      .infinite-scroll-trigger .btn {\n        pointer-events: none;\n        .infinite-scroll-trigger-content {\n          display: none;\n        }\n        .infinite-scroll-trigger-content-nomore {\n          display: block;\n        }\n      }\n    }\n\n    /* .infinite-scroll-pre */\n\n    .infinite-scroll-pre {\n      display: none;\n    }\n\n    .infinite-scroll-reset {\n      .infinite-scroll-pre {\n        display: block;\n      }\n    }\n\n  }\n  /*##END*/\n}\n";