export default "import { Xt } from 'xtendui'\n\nXt.mount.push({\n  matches: '.demo--toggle-timing',\n  mount: object => {\n    // init\n\n    let self = new Xt.Toggle(object, {\n      delayOn: current => {\n        return Math.min(current * 150, 300)\n      },\n      delayOff: (current, total) => {\n        return Math.min((total - current) * 150, 300)\n      },\n    })\n\n    // unmount\n\n    return () => {\n      self.destroy()\n      self = null\n    }\n  },\n})\n\nXt.mount.push({\n  matches: '.demo--toggle-timing--event',\n  mount: object => {\n    // init\n\n    let self = new Xt.Toggle(object, {\n      on: 'mouseenter',\n      off: 'mouseleave',\n      delayOn: current => {\n        return Math.min(current * 150, 300)\n      },\n      delayOff: (current, total) => {\n        return Math.min((total - current) * 150, 300)\n      },\n    })\n\n    // unmount\n\n    return () => {\n      self.destroy()\n      self = null\n    }\n  },\n})\n";