export default "module.exports = {\n  component: theme => ({\n    '.nav-sides': {\n      // setup\n      cursor: 'none',\n      position: 'absolute',\n      zIndex: theme('zIndex.top'),\n      top: '0',\n      height: '100%',\n      '.slider.xt-disabled &': {\n        display: 'none',\n      },\n      // styles\n      width: '6rem',\n      // animation\n      '.nav-sides-btn > *': {\n        transitionProperty: 'opacity, transform',\n        transitionDuration: theme('transitionDuration.200'),\n        transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n      },\n      '&:active': {\n        '.nav-sides-btn > *': {\n          opacity: '.75',\n          transform: 'scale(0.8)',\n        },\n      },\n    },\n    '.nav-sides-btn': {\n      // setup\n      padding: '0',\n      lineHeight: '0',\n      // animation\n      opacity: '0',\n      transitionProperty: 'opacity',\n      transitionDuration: theme('transitionDuration.200'),\n      transitionTimingFunction: theme('transitionTimingFunction.in-out'),\n      '&.in': {\n        opacity: '1',\n      },\n    },\n    '@media (hover: none)': {\n      '.nav-sides': {\n        // styles\n        width: '3rem',\n      },\n      '.nav-sides-btn': {\n        // styles\n        '@apply transform-none': '',\n        position: 'absolute',\n        top: '50%',\n        left: '50%',\n        marginTop: '-0.7rem',\n        marginLeft: '-0.7rem',\n        opacity: '1',\n      },\n    },\n  }),\n}\n";