export default "/**\n * sidebar\n*/\n\n.gatsby_site_article_sidebar {\n  @apply text-white links-inverse overflow-sub;\n  background: theme('colors.accent.500');\n  @screen lg {\n    position: fixed;\n    z-index: 2;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    overflow-x: hidden;\n    overflow-y: scroll;\n    line-height: 1;\n  }\n  @screen lg {\n    width: 18rem;\n  }\n  @screen xl {\n    width: 20rem;\n  }\n}\n\n/**\n * header\n*/\n\n.gatsby_logo-icon {\n  display: inline-flex;\n  align-items: center;\n  height: 100%;\n  img {\n    width: 8.5rem;\n  }\n  img {\n    @apply transition;\n  }\n  &:hover, &:active {\n    img {\n      opacity: 0.9;\n    }\n  }\n}\n\n.gatsby_site_header {\n  @apply text-white links-inverse;\n  padding-top: 2rem;\n  padding-bottom: 1.25rem;\n  padding-left: 1.5rem;\n  padding-right: 1.5rem;\n  @screen xl {\n    padding-left: 2rem;\n    padding-right: 2rem;\n  }\n}\n\n.gatsby_site_header_content {\n  @apply w-full list;\n  align-items: flex-end;\n  justify-content: space-between;\n}\n\n.gatsby_site_header_top_social {\n  @apply list list-0.5;\n  align-items: stretch;\n}\n\n.gatsby_btn-site_header_top_social {\n  margin-top: -5px;\n  margin-bottom: -5px;\n  padding: 5px;\n  border-radius: 0;\n  @apply text-white links-inverse;\n  line-height: 1;\n  &:last-child {\n    padding-right: 0;\n  }\n  @apply transition;\n  opacity: 0.8;\n  &:hover {\n    opacity: 1;\n    transform: scale(1.2);\n  }\n  &:active {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n.gatsby_site_header_listing {\n  padding-top: 2rem;\n  padding-bottom: 1.5rem;\n  padding-left: 1.5rem;\n  padding-right: 1.5rem;\n  @screen xl {\n    padding-left: 2rem;\n    padding-right: 2rem;\n  }\n}\n\n.gatsby_site_header_cat {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  justify-content: flex-start;\n}\n\n.gatsby_site_header_inner, .gatsby_site_header_cat, .gatsby_site_header_item, .gatsby_site_header_adiacent, .gatsby_site_header_adiacent_inner {\n  position: relative;\n}\n\n.gatsby_site_header_sub {\n  @apply w-full;\n}\n\n.gatsby_site_header_adiacent, .gatsby_site_header_adiacent_inner {\n  @apply w-full;\n  display: none;\n  &.active {\n    display: block;\n  }\n}\n\n.gatsby_cat--site_article_sidebar {\n  font-weight: theme('fontWeight.semibold');\n  margin-top: 1rem !important;\n  margin-bottom: 0.5rem !important;\n  @apply text-white links-inverse;\n  .gatsby_site_header_listing > *:first-child & {\n    margin-top: 0 !important;\n  }\n}\n\n.gatsby_site_header_links_container {\n  padding-left: 1.5rem;\n  padding-right: 1.5rem;\n  @screen xl {\n    padding-left: 2rem;\n    padding-right: 2rem;\n  }\n}\n\n.gatsby_site_header_links {\n  @apply border-t border-b border-accent-900 border-opacity-25;\n  padding-top: 1.25rem;\n  padding-bottom: 1.25rem;\n  > * {\n    margin-left: -.5rem;\n    margin-right: -.5rem;\n  }\n}\n\n.gatsby_site_header_sub {\n  margin-left: -.5rem;\n  margin-right: -.5rem;\n}\n\n.gatsby_site_header_adiacent {\n  .gatsby_site_header_item {\n    padding-left: .5rem;\n  }\n}\n\n.gatsby_site_header_adiacent_inner {\n  padding-top: 3px;\n  padding-bottom: 3px;\n  .gatsby_site_header_item {\n    padding-left: .5rem;\n  }\n}\n\n.gatsby_btn-site_article_sidebar, .gatsby_btn-site_header_link {\n  pointer-events: auto !important;\n  padding: 0;\n  padding-bottom: 1px;\n  border-width: 0;\n  @apply w-full font-sans;\n  > span {\n    @apply transition duration-500 ease-in-out-quint hover:duration-300 hover:ease-out-quint active:ease-out-quint;\n    border-radius: 6px;\n    padding: .3125rem .5rem;\n    font-weight: theme('fontWeight.normal');\n    font-size: 0.8125rem;\n    color: rgba(255, 255, 255, 0.66);\n    line-height: 1;\n    letter-spacing: theme('letterSpacing.wide');\n    text-transform: none;\n  }\n  &.gatsby_btn-site_header_link {\n    > span {\n      @apply w-full;\n      font-weight: theme('fontWeight.semibold');\n      text-transform: uppercase;\n    }\n  }\n  &.gatsby_btn-site_article_sidebar--sub {\n    > span {\n      @apply w-full;\n    }\n  }\n  &.gatsby_btn-site_article_sidebar--adiacent {\n    > span {\n      @apply bg-accent-900 bg-opacity-25;\n      font-size: 12px;\n    }\n  }\n  &.gatsby_btn-site_article_sidebar--adiacent_inner {\n    padding-bottom: 0;\n    > span {\n      font-size: 12px;\n    }\n  }\n  &:hover {\n    > span {\n      @apply bg-accent-900 bg-opacity-25;\n      transform: translateX(.25rem);\n      color: theme('colors.white');\n    }\n  }\n  &:active, &.active, &.current {\n    > span {\n      @apply bg-accent-900 bg-opacity-50;\n      transform: none;\n      font-weight: theme('fontWeight.semibold');\n      color: theme('colors.white');\n    }\n  }\n}\n";