export default "#iframe--stores-listing-v1 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    .listing-front {\n      position: absolute;\n      top: 0;\n      left: 0;\n      bottom: 0;\n      right: 0;\n      z-index: theme('zIndex.active');\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n    }\n\n    .listing-back {\n      position: relative;\n      min-height: 22rem;\n      transform: rotateY(180deg);\n      -webkit-backface-visibility: hidden;\n      backface-visibility: hidden;\n    }\n\n    .listing-item {\n      perspective: 1400px;\n      .listing-item_inner {\n        height: 100%;\n        transform-style: preserve-3d;\n        @apply transition ease-in-out-quad duration-500;\n      }\n      &.active {\n        .listing-item_inner {\n          transform: rotateY(180deg);\n        }\n        .listing-back {\n          z-index: theme('zIndex.active');\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";