export default "import path from 'path'\nconst btnPrimary = require('components/snippets/classes/btn-primary').default\nconst cardDefaultOverlay = require('components/snippets/classes/card-default-overlay').default\nconst iconX = require('components/snippets/icons').iconX\nconst iconPackage = require('components/snippets/icons').iconPackage\n\nconst filename = __filename.replace(/\\\\/g, '/')\nconst dirs = path.dirname(filename).split('/')\nexport const demo = {\n  name: path.basename(filename, '.js'),\n  dirs: dirs,\n}\n\ndemo.htmlSource = `\n<div class=\"list list-2 items-center\">\n\n  <div data-xt-overlay>\n\n    <button type=\"button\" class=\"btn btn-md rounded-md ${btnPrimary()}\">\n      list\n    </button>\n\n    <div class=\"overlay\">\n      <div class=\"overlay-container max-w-3xl\">\n        <div class=\"overlay-inner\">\n\n          <div class=\"card rounded-md shadow-overlay ${cardDefaultOverlay()}\">\n            <div class=\"btn btn-close p-5 text-2xl\" aria-label=\"Close\">\n              ${iconX()}\n            </div>\n            <div class=\"card-md\">\n              <div class=\"list list-3 flex-nowrap items-baseline\">\n                <div>\n                  ${iconPackage({ classes: 'icon-lg text-2xl' })}\n                </div>\n                <div>\n                  <div class=\"h4\">Lorem ipsum</div>\n                  <p><strong>Lorem ipsum</strong> dolor sit amet, <a href=\"#\">consectetur adipiscing</a> elit. Nullam suscipit, velit eu tristique mollis, dui felis dictum turpis, a auctor est odio ac diam. Sed mauris augue, sagittis vitae magna eget, vehicula scelerisque elit.</p>\n                </div>\n              </div>\n            </div>\n          </div>\n\n        </div>\n      </div>\n    </div>\n\n  </div>\n\n</div>\n`\n";