export default "#iframe--stores-listing-v2 {\n  /*##START*/\n  body { /* add your own selector instead of body to contain the code */\n\n    .listing-item {\n      position: relative;\n      overflow: hidden;\n      @media (max-width: theme('screens.md')) {\n        .listing-front {\n          .listing-item_content {\n            display: none;\n          }\n        }\n      }\n      @screen md {\n        .listing-front {\n          display: block;\n          position: absolute;\n          top: 0;\n          left: 0;\n          width: 100%;\n          height: 100%;\n        }\n        .listing-back {\n          pointer-events: none;\n          opacity: 0;\n        }\n        &.active {\n          .listing-back {\n            pointer-events: all;\n          }\n        }\n      }\n      .listing-front {\n        @media (max-width: theme('screens.md')) {\n          opacity: 1 !important;\n          transform: none !important;\n        }\n        @screen md {\n          cursor: pointer;\n        }\n      }\n      .listing-back {\n        @media (max-width: theme('screens.md')) {\n          opacity: 1 !important;\n          transform: none !important;\n          margin-top: theme('spacing.6');\n          .btn-close {\n            display: none;\n          }\n        }\n        @screen md {\n          min-height: 22rem;\n        }\n      }\n    }\n\n    .listing-asset {\n      .media-container {\n        @supports (mix-blend-mode: screen) {\n          &:before {\n            content: '';\n            position: absolute;\n            z-index: theme('zIndex.active');\n            top: 0;\n            left: 0;\n            right: 0;\n            bottom: 0;\n            background: #000000;\n            mix-blend-mode: saturation;\n          }\n          &:after {\n            content: '';\n            position: absolute;\n            z-index: theme('zIndex.active');\n            top: 0;\n            left: 0;\n            right: 0;\n            bottom: 0;\n            background: #7686FF;\n            mix-blend-mode: screen;\n            opacity: .25;\n          }\n        }\n      }\n    }\n\n  }\n  /*##END*/\n}\n";