export default "module.exports = {\n  component: theme => ({\n    '.loader': {\n      // setup\n      position: 'absolute',\n      zIndex: theme('zIndex.top'),\n      top: '0',\n      left: '0',\n      bottom: '0',\n      right: '0',\n      borderRadius: 'inherit !important',\n      overflow: 'hidden',\n    },\n    '.filler': {\n      // setup\n      position: 'absolute',\n      top: '0',\n      left: '0',\n      bottom: '0',\n      right: '0',\n      margin: 'auto',\n      span: {\n        position: 'absolute',\n        top: '0',\n        left: '0',\n      },\n    },\n    '.spinner': {\n      // setup\n      position: 'absolute',\n      top: '0',\n      left: '0',\n      bottom: '0',\n      right: '0',\n      margin: 'auto',\n      // styles\n      width: '25px',\n      height: '25px',\n      lineHeight: '0',\n      '.btn &': {\n        width: '1em',\n        height: '1em',\n      },\n      svg: {\n        circle: {\n          fill: 'none',\n          strokeWidth: '30',\n          transformBox: 'border-box',\n          transformOrigin: 'center center',\n          '.btn &': {\n            strokeWidth: '40',\n          },\n        },\n        '&:nth-child(1)': {\n          position: 'relative',\n          zIndex: theme('zIndex.active'),\n          circle: {\n            strokeDashoffset: '628',\n            transform: 'rotateY(0deg) rotateZ(-90deg)',\n          },\n        },\n        '&:nth-child(2)': {\n          position: 'absolute',\n          top: '0',\n          left: '0',\n          circle: {\n            strokeDashoffset: '0',\n          },\n        },\n      },\n    },\n    '.loader-x': {\n      // setup\n      '.filler': {\n        span: {\n          width: '100%',\n          height: '100%',\n        },\n        // styles\n        'span:nth-child(1)': {\n          width: '0',\n        },\n      },\n    },\n    '.loader-y': {\n      // setup\n      '.filler': {\n        span: {\n          width: '100%',\n          height: '100%',\n        },\n        // styles\n        'span:nth-child(1)': {\n          height: '0',\n        },\n      },\n    },\n    '.loader-size-top': {\n      // setup\n      '.filler': {\n        bottom: 'auto',\n        span: {\n          top: '0',\n        },\n      },\n    },\n    '.loader-size-bottom': {\n      // setup\n      '.filler': {\n        top: 'auto',\n        span: {\n          bottom: '0',\n        },\n      },\n    },\n    '.loader-size-left': {\n      // setup\n      '.filler': {\n        right: 'auto',\n        span: {\n          left: '0',\n        },\n      },\n    },\n    '.loader-size-right': {\n      // setup\n      '.filler': {\n        left: 'auto',\n        span: {\n          right: '0',\n        },\n      },\n    },\n    '.loader-size-x, .loader-size-top, .loader-size-bottom': {\n      // styles\n      '.filler': {\n        height: '3px',\n      },\n    },\n    '.loader-size-y, .loader-size-left, .loader-size-right': {\n      // styles\n      '.filler': {\n        width: '3px',\n      },\n    },\n    '.loader-size-x': {\n      // styles\n      '.filler': {\n        left: '25%',\n        right: '25%',\n      },\n    },\n    '.loader-size-y': {\n      // styles\n      '.filler': {\n        top: '25%',\n        bottom: '25%',\n      },\n    },\n    '.spinner-animated': {\n      // animation\n      svg: {\n        '&:nth-child(1)': {\n          circle: {\n            animation: 'spinner 1500ms infinite',\n          },\n        },\n      },\n    },\n    '.loader-x-animated': {\n      // animation\n      '.filler': {\n        'span:nth-child(1)': {\n          animation: 'filler-x 1500ms infinite',\n        },\n      },\n    },\n    '.loader-y-animated': {\n      // animation\n      '.filler': {\n        'span:nth-child(1)': {\n          animation: 'filler-y 1500ms infinite',\n        },\n      },\n    },\n  }),\n}\n";