export default ".demo--slider-automaticscroll {\n\n  /* slider */\n\n  .slider {\n    &.xt-overflow-auto {\n      .slides-inner {\n        justify-content: center;\n      }\n    }\n  }\n\n  .slider--img {\n    .slide_item {\n      width: 300px;\n      @apply transform transition;\n      &:hover, &:active, &.active {\n        transform: scale(0.98);\n      }\n    }\n  }\n\n  .slider--text {\n    .slide_item {\n      @apply transform transition;\n      &:hover, &:active, &.active {\n        color: theme('colors.accent.500');\n      }\n    }\n  }\n\n}\n";