export default "module.exports = {\n  component: theme => ({\n    'html.xt-scrollbar': {\n      // setup\n      body: {\n        overflowY: 'hidden !important',\n      },\n      '.overlay': {\n        overflowY: 'scroll !important',\n      },\n    },\n    '.initial': {\n      // instant animations on components init and reinit\n      '&, *': {\n        '@apply duration-none !important': '',\n      },\n    },\n    '@media (prefers-reduced-motion: reduce), (update: slow)': {\n      // instant animations accessibility\n      '*': {\n        '@apply duration-none !important': '',\n      },\n    },\n    '[data-xt-pag].xt-ignore': {\n      // setup\n      display: 'none !important',\n    },\n    '[data-xt-nav]': {\n      // setup\n      cursor: 'pointer',\n    },\n    '.xt-transition-none': {\n      // setup\n      transition: 'none !important',\n    },\n    '.xt-container': {\n      // setup\n      position: 'relative',\n      width: '100%',\n    },\n    '.xt-disable': {\n      // setup\n      '&:after': {\n        display: 'none',\n        content: '\"xt-disable\"',\n      },\n    },\n    '.xt-disable-after-init': {\n      // setup\n      '&:after': {\n        display: 'none',\n        content: '\"xt-disable-after-init\"',\n      },\n    },\n    '.xt-jump': {\n      // setup\n      '&:not(.in)': {\n        cursor: 'pointer',\n      },\n      '&.xt-jumps-none, .xt-jumps-none &': {\n        cursor: 'inherit',\n      },\n    },\n    '.xt-pointer-events-none': {\n      // setup\n      pointerEvents: 'none',\n    },\n    '.xt-links-none': {\n      // setup\n      'a&, a': {\n        pointerEvents: 'none',\n      },\n    },\n    '.xt-grab': {\n      // setup\n      '@apply select-none': '',\n      cursor: 'grab',\n      'a, img': {\n        // prevent browser drag\n        '-webkit-user-drag': 'none',\n        userDrag: 'none',\n        // prevent browser selection\n        '-webkit-user-select': 'none',\n        '-moz-user-select': 'none',\n        '-ms-user-select': 'none',\n        userSelect: 'none',\n      },\n    },\n    '.xt-calculating': {\n      // setup\n      '@apply duration-none !important': '',\n      display: 'block !important',\n      visibility: 'hidden !important',\n      opacity: '0 !important',\n    },\n    '.xt-collapse-reset': {\n      // setup\n      overflow: 'visible !important',\n    },\n    '.xt-indicator': {\n      pointerEvents: 'none',\n      position: 'fixed',\n      right: '0',\n      marginTop: '-9px',\n      width: '100px',\n      height: '18px',\n      zIndex: theme('zIndex.top') + 1,\n      lineHeight: '0',\n      textAlign: 'right',\n      '&:before': {\n        display: 'inline-block',\n        position: 'relative',\n        padding: '3px 6px',\n        fontSize: '12px',\n        lineHeight: '1',\n        textTransform: 'uppercase',\n      },\n    },\n    '.xt-indicator-trigger': {\n      right: '100px',\n      backgroundColor: 'purple',\n      '&:before': {\n        content: '\"trigger\"',\n        color: 'white',\n        backgroundColor: 'purple',\n      },\n    },\n    '.xt-indicator-start': {\n      backgroundColor: 'red',\n      '&:before': {\n        content: '\"start\"',\n        color: 'white',\n        backgroundColor: 'red',\n      },\n    },\n    '.xt-indicator-end': {\n      backgroundColor: 'blue',\n      '&:before': {\n        content: '\"end\"',\n        color: 'white',\n        backgroundColor: 'blue',\n      },\n    },\n    '.xt-indicator-start-real': {\n      backgroundColor: 'red',\n      opacity: '.3',\n      zIndex: theme('zIndex.top'),\n      '&:before': {\n        content: '\"start real\"',\n        color: 'white',\n        backgroundColor: 'red',\n      },\n    },\n    '.xt-indicator-end-real': {\n      backgroundColor: 'blue',\n      opacity: '.3',\n      zIndex: theme('zIndex.top'),\n      '&:before': {\n        content: '\"end real\"',\n        color: 'white',\n        backgroundColor: 'blue',\n      },\n    },\n  }),\n}\n";