export default "import { Xt } from '../xt.js'\nimport './toggle'\nimport JSON5 from 'json5'\n\n/**\n * Overlay\n */\nclass Overlay extends Xt.Toggle {\n  /**\n   * constructor\n   * @param {Node|HTMLElement|EventTarget|Window} object Base node\n   * @param {Object} optionsCustom User options\n   * @constructor\n   */\n  constructor(object, optionsCustom = {}) {\n    super(object, optionsCustom)\n  }\n\n  //\n  // init\n  //\n\n  /**\n   * init aria\n   */\n  initAriaRole() {\n    const self = this\n    const options = self.options\n    // aria\n    if (options.aria) {\n      // role\n      if (self.targets.length) {\n        for (const el of self.elements) {\n          const ariaEls = Xt.queryAll(el, options.ariaControls)\n          const ariaEl = ariaEls.length ? ariaEls[0] : el\n          ariaEl.setAttribute('aria-haspopup', 'dialog')\n        }\n        for (const tr of self.targets) {\n          tr.setAttribute('role', 'dialog')\n          tr.setAttribute('aria-modal', 'true')\n        }\n      } else {\n        for (const el of self.elements) {\n          el.setAttribute('role', 'dialog')\n          el.setAttribute('aria-modal', 'true')\n        }\n      }\n    }\n  }\n\n  //\n}\n\n//\n// options\n//\n\nOverlay.componentName = 'xt-overlay'\nOverlay.optionsDefault = {\n  // element\n  elements: ':scope > a, :scope > button',\n  targets: ':scope > .overlay',\n  // class\n  class: 'in in-overlay',\n  // quantity\n  min: 0,\n  max: 1,\n  // event\n  on: 'click',\n  eventLimit: '.event-limit, :scope > .overlay-container > .overlay-inner > *:first-child',\n  // timing\n  instant: {\n    elements: true,\n    targets: false,\n    elementsInner: true,\n    targetsInner: false,\n  },\n  // other\n  backdrop: true,\n  closeAuto: true,\n  appendTo: 'body',\n  classHtml: false,\n  closeInside: '.overlay-dismiss, .backdrop, .btn-close',\n  closeOnDisable: true,\n  scrollbar: true,\n  focusLimit: true,\n}\n\n//\n// export\n//\n\nXt.Overlay = Overlay\n\n//\n// observe\n//\n\nXt.mount.push({\n  matches: `[data-${Xt.Overlay.componentName}]`,\n  mount: object => {\n    // vars\n\n    const optionsMarkup = object.getAttribute(`data-${Xt.Overlay.componentName}`)\n    const options = optionsMarkup ? JSON5.parse(optionsMarkup) : {}\n\n    // init\n\n    let self = new Xt.Overlay(object, options)\n\n    // unmount\n\n    return () => {\n      self.destroy()\n      self = null\n    }\n  },\n})\n";