export default "/**\n * Tailwind CSS theme for `prism.js`\n * Based on the syntax theme used in the Tailwind CSS docs, which is based on Atom's\n * `atom-dark` theme: https://github.com/atom/atom-dark-syntax\n * @author Taylor Bryant\n */\n\n code[class*=\"language-\"],\n pre[class*=\"language-\"] {\n   font-family: Menlo, Monaco, Consolas, \"Liberation Mono\", \"Courier New\", monospace;\n   -webkit-overflow-scrolling: touch;\n   -webkit-font-smoothing: auto;\n   -moz-osx-font-smoothing: auto;\n   color: #fff;\n   color: #edf2f7;\n   direction: ltr;\n   text-align: left;\n   white-space: pre;\n   word-spacing: normal;\n   word-break: normal;\n   line-height: 1.5;\n   tab-size: 4;\n   hyphens: none;\n }\n\n pre[class*=\"language-\"] {\n   overflow: auto;\n   padding: 1rem;\n   border-radius: 0.5rem;\n }\n\n :not(pre) > code[class*=\"language-\"],\n pre[class*=\"language-\"] {\n   background-color: #2d3748;\n   font-size: 0.875rem;\n }\n\n code.language-js .token.regex,\n pre.language-js .token.regex,\n .token.selector,\n .token.attr-name,\n .token.property,\n .token.regex,\n .token.class-name,\n .token.entity {\n   color: #ffe484;\n }\n\n code.language-diff,\n pre.language-diff {\n   color: #cbd5e0;\n }\n\n code.language-diff .token.inserted,\n pre.language-diff .token.inserted,\n .token.url,\n .token.attr-value,\n .token.char,\n .token.string,\n .token.builtin,\n .token.inserted,\n .token.boolean,\n .token.constant {\n   color: #b5f4a5;\n }\n\n :not(pre) > code[class*=\"language-\"] {\n   padding: 0.1em;\n   border-radius: 0.3em;\n }\n\n .token.comment,\n .token.prolog,\n .token.doctype,\n .token.cdata {\n   color: #a0aec0;\n }\n\n .namespace {\n   opacity: 0.7;\n }\n\n .token.symbol,\n .token.deleted,\n .token.tag,\n .token.number,\n code.language-js .token.boolean,\n pre.language-js .token.boolean {\n   color: #ff8383;\n }\n\n .token.unit,\n .token.function,\n .token.punctuation,\n .token.hexcode,\n .token.operator,\n code.language-js .token.operator,\n pre.language-js .token.operator {\n   color: #93ddfd;\n }\n\n .token.atrule,\n .token.atrule > .token.property,\n .token.atrule > .token.property + .token.punctuation,\n .token.atrule\n   > .token.property\n   + .token.punctuation\n   + .token.number\n   + .token.unit,\n .token.atrule > .token.number,\n .token.atrule > .token.unit,\n code.language-js .token.punctuation,\n pre.language-js .token.punctuation,\n code.language-js .token.keyword,\n pre.language-js .token.keyword,\n code.language-bash .token.function,\n pre.language-bash .token.function {\n   color: #fff;\n }\n\n .token.rule,\n .token.important,\n .token.variable {\n   font-weight: inherit;\n   color: #d9a9ff;\n }\n\n .token.bold {\n   font-weight: bold;\n }\n\n .token.italic {\n   font-style: italic;\n }\n\n .token.entity {\n   cursor: help;\n }\n";